/* global FB */
import * as React from "react";
import type { HeadFC } from "gatsby";
import { navigate, graphql } from "gatsby";
import Tab from "../components/tab/tab";
import ProductCard from "../components/card/NumberCard";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import { useDispatch } from "react-redux";
import { updateCart } from "../helpers/action";
import { shallowEqual, useSelector } from "react-redux";

const TabContent = ({ items }) => {
  return (
    <div className="row">
      {items.map((item, i) => (
        <div key={i} className="col-sm-4">
          <ProductCard data={item} />
        </div>
      ))}
    </div>
  );
};

const IndexPage = ({ data }) => {
  const { useState, useEffect } = React;
  const [query, setQuery] = useState("");
  const [lastUpdated, setLastUpdated] = React.useState("");
  const [message, setMessage] = useState("Loading...")
  const [products, setProducts] = useState([]);
  const [queriedProducts, setQueriedProducts] = useState([]);
  useEffect(() => {
    Parse?.Cloud?.run("getAllNumber").then((e) => {
      setLastUpdated(e[0] && e[0].get("createdAt").toString().substr(0, 16));
      const _products = e.map((node) => ({
        ...node.get("data"),
      }));
      setProducts(_products);
      setQueriedProducts(_products);
      setMessage("")
    });
  }, []);

  useEffect(() => {
    setQueriedProducts(products.filter((n) => n.num.includes(query)));
  }, [query]);

  return (
    <>
      <section className="flash__sell pt-25 pb-40 grey-bg-2">
        <div className="container">
          <div className="features__inner">
            <div className="row row-cols-xxl-5 row-cols-xl-5 row-cols-lg-3 row-cols-md-2 row-cols-sm-2 row-cols-1 gx-0">
              <div className="col">
                <div className="features__item d-flex white-bg">
                  <div className="features__icon mr-15">
                    <i className="fal fa-rocket-launch"></i>
                  </div>
                  <div className="features__content">
                    <h6>Pick a number</h6>
                    <p>From the various Singapore telco.</p>
                  </div>
                </div>
              </div>
              <div className="col">
                <div className="features__item d-flex white-bg">
                  <div className="features__icon mr-15">
                    <i className="fal fa-sync"></i>
                  </div>
                  <div className="features__content">
                    <h6>Telegram</h6>
                    <p></p>
                  </div>
                </div>
              </div>
              <div className="col">
                <div className="features__item d-flex white-bg">
                  <div className="features__icon mr-15">
                    <i className="fal fa-user-headset"></i>
                  </div>
                  <div className="features__content">
                    <h6>Make Payment</h6>
                    <p>Pay a flat fee (excluding registration)</p>
                  </div>
                </div>
              </div>
              <div className="col">
                <div className="features__item d-flex white-bg">
                  <div className="features__icon mr-15">
                    <i className="fal fa-thumbs-up"></i>
                  </div>
                  <div className="features__content">
                    <h6>Register</h6>
                    <p>We will follow up with you the registration process</p>
                  </div>
                </div>
              </div>
              <div className="col">
                <div className="features__item features__item-last d-flex white-bg">
                  <div className="features__icon mr-15">
                    <i className="fal fa-badge-dollar"></i>
                  </div>
                  <div className="features__content">
                    <h6>Completed</h6>
                    <p>If number is not available, we will do a refund.</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <br />
        </div>
        <div className="container">
          <div className="row">
            <div className="col-xl-12">
              {!queriedProducts.length && <h3> { message } </h3>}
            </div>
          </div>
        </div>

        <Tab
          onFilterChanged={(query) => setQuery(query)}
          title={`Pick A Number`}
          titleBold={`${queriedProducts.length} Numbers to choose | Last Updated: ${lastUpdated}`}
          tabObject={{
            groupbuy: <TabContent items={queriedProducts} />,
          }}
        />
      </section>
    </>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;

export const query = graphql`
  query ($language: String!) {
    locales: allLocale(filter: { language: { eq: $language } }) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }

    products: allMobile {
      edges {
        node {
          id,
          num
        }
      }
    }
  }
`;
